@font-face {
  font-family: "BebasNeue-Regular";
  src: url("../fonts/BebasNeue-Regular.woff2") format("woff2"), url("../fonts/BebasNeue-Regular.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "RobotoCondensed-Regular";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "RobotoCondensed-Bold";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

* {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 1.29;
  font-weight: 400;
  font-family: "Arial", sans-serif;
  color: #444444;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal; }

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1250px;
  width: 100%;
  min-width: 320px;
  padding: 0 45px;
  margin: 0 auto;
  box-sizing: border-box; }
  @media (max-width: 1199px) {
    .container {
      padding: 0 45px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 15px; } }

.page-header {
  background-color: #003d85;
  background-image: url("../img/header-background.svg");
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff; }

.page-header__wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
  background-image: url("../img/trainer-main@1x.png");
  background-position: top 50px left calc(50% + 130px);
  background-repeat: no-repeat;
  background-size: 500px, 556px; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header__wrapper {
      background-image: url("../img/trainer-main@2x.png"); } }

.page-header__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.page-header__phone {
  margin: 0;
  font-family: "BebasNeue-Regular", "Arial", sans-serif;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.03em; }

.page-header__wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end; }

.page-header__social__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-bottom: 85px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }

.page-header__social__item {
  cursor: pointer;
  text-align: right; }
  .page-header__social__item--facebook {
    padding-bottom: 15px; }
  .page-header__social__item--vkontakte {
    padding-bottom: 12px; }
  .page-header__social__item--facebook svg {
    padding-left: 3px; }
  .page-header__social__item--vkontakte svg {
    padding-left: 1px; }
  .page-header__social__item--instagram svg {
    padding-left: 4px; }

.header-description {
  display: flex;
  flex-direction: column;
  width: 300px;
  justify-content: flex-end;
  margin-left: 97px; }

.header-description__city {
  font-family: "BebasNeue-Regular", "Arial", sans-serif;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  margin: 0;
  padding-top: 100px; }

.header-description__list {
  list-style: none;
  font-family: "RobotoCondensed-Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 150%;
  text-align: right;
  margin: 0; }

.header-description__header {
  font-family: "BebasNeue-Regular", "Arial", sans-serif;
  font-size: 109px;
  line-height: 109px;
  letter-spacing: 0.03em;
  text-align: left;
  margin: 0; }

.header-description__link {
  font-family: "RobotoCondensed-Bold", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  display: flex;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 4px solid #ffffff;
  min-width: 234px;
  padding-top: 24px;
  padding-bottom: 24px;
  padding-left: 10px;
  padding-right: 10px;
  text-align: center;
  cursor: pointer;
  align-self: flex-end;
  margin-bottom: 96px; }

.description-block {
  color: #102654; }

.description-block h2 {
  font-family: "BebasNeue-Regular", "Arial", sans-serif;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em; }
